body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-3xs: 10px;
  --font-size-xl: 20px;

  /* Colors */
  --color-gray-100: #212541;
  --color-gray-200: #1f233f;
  --color-gray-300: rgba(255, 255, 255, 0);
  --color-darkslategray: #272b4a;
  --color-darkslateblue-100: #4f5794;
  --color-darkslateblue-200: #343961;
  --color-darkslateblue-300: #31365e;
  --color-white: #fff;
  --color-lavender: #e2e4f5;
  --color-lightsteelblue-100: #b4bae9;
  --color-lightsteelblue-200: #a6acdc;
  --color-deepskyblue-100: #00baff;
  --color-cornflowerblue: #6a73b5;
  --color-aquamarine: #12eeb1;
  --color-orange: #ffa800;
  --color-darkolivegreen: #8e7c3b;
  --color-gold: #ffcf00;
  --color-mediumpurple: #b375da;
  --color-darkviolet: #a914ff;
  --color-violet: #d896ff;

  /* Gaps */
  --gap-8xs: 5px;
  --gap-9xs: 4px;
  --gap-10xs-4: 2.4px;
  --gap-11xs-8: 1.8px;
  --gap-10xs-1: 2.1px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-21xl: 40px;
  --padding-mini-5: 14.5px;
  --padding-mini: 15px;
  --padding-12xs: 1px;
  --padding-8xs: 5px;
  --padding-6xs: 7px;
  --padding-9xs: 4px;
  --padding-2xs-3: 10.3px;
  --padding-4xs: 9px;
  --padding-2xs-1: 10.1px;
  --padding-2xs-2: 10.2px;
  --padding-4xs-3: 8.3px;
  --padding-7xs: 6px;
  --padding-10xl-5: 29.5px;
  --padding-5xs-5: 7.5px;
  --padding-13xl-7: 32.7px;
  --padding-lg-5: 18.5px;
  --padding-mid-5: 17.5px;

  /* border radiuses */
  --br-7xs: 6px;
  --br-lg: 18px;
  --br-9xs: 4px;
  --br-3xs: 10px;
  --br-smi: 13px;
  --br-76xl-5: 95.5px;
  --br-31xl: 50px;
  --br-41xl: 60px;
  --br-46xl: 65px;
}
